import { defHttp } from '/@/utils/http/axios';

enum Api {
  //累计发电统计
  getPowerStatUrl = '/da/group-operation/overview/powerStat',

  //电站电量统计
  getPowerStatisticsUrl = '/da/group-operation/overview/power/statistics',

  //社会贡献
  getSocialConStatUrl = '/da/group-operation/overview/socialConStat',

  //顶端指标
  getTopInfoUrl = '/da/group-operation/overview/topInfo',

  //实时功率
  getActivePowerUrl = '/da/group-operation/overview/activePower',

  //发电效率
  getPowerEfficiencyUrl = '/da/group-operation/overview/powerEfficiency',

  // //电站运营
  // getPowerRunUrl = '/da/group-operation/overview/runData',

  //灾害预警
  getDisasterWarningUrl = '/da/group-operation/overview/disasterWarning',
}

export const getPowerStatData = (params) => defHttp.post({ url: Api.getPowerStatUrl, params }, {});

export const getPowerStatistics = (params) =>
  defHttp.post({ url: Api.getPowerStatisticsUrl, params }, {});

export const getSocialConStatData = (params) =>
  defHttp.post({ url: Api.getSocialConStatUrl, params }, {});

export const getTopInfoData = (params) => defHttp.post({ url: Api.getTopInfoUrl, params }, {});

export const getActivePowerData = (params) =>
  defHttp.post({ url: Api.getActivePowerUrl, params }, {});

export const getPowerEfficiency = (params) =>
  defHttp.post({ url: Api.getPowerEfficiencyUrl, params }, {});

// export const getPowerRunData = (params) => defHttp.post({ url: Api.getPowerRunUrl, params }, {});

export const getDisasterWarningData = (params) =>
  defHttp.post({ url: Api.getDisasterWarningUrl, params }, {});

